.DeleteButton {
  background: inherit;
  background-color: #fff;
  border: inherit;
  border-radius: 50%;
  width: 1.5rem;
  height: 1.5rem;
  font-size: 1rem;
  text-align: center;
  padding: 0;
  transition: all 0.2s ease-in-out;
}

.DeleteButton:disabled {
  color: #fff;
}

.DeleteButton:not(:disabled):hover,
.DeleteButton:not(:disabled):focus {
  color: #fff;
  background-color: #f50057;
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
}

.Item {
  display: flex;
  flex-direction: column;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: white;
}

.Item.is-selected {
  outline: none;
  box-shadow: -3px 5px 10px -1px #ddd, 3px -5px 10px -1px #ddd;
}

.Item__toolbar {
  background-color: #ddd;
  border-bottom: 1px solid #ddd;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  padding: 5px;
}

.Item__name {
  font-family: "Arial", sans-serif;
  display: inline-block;
  margin-left: 10px;
  overflow: hidden;
  width: 75%;
  vertical-align: text-bottom;
  text-overflow: ellipsis;
}

.Item.is-selected .Item__toolbar {
  color: #fff;
  background-color: #3f51b5;
}

.Item__childrenContainer {
  /* This way children have all the container height available */
  height: 100%;
  padding: 10px;
  overflow: auto;
}

.center {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.scrollable {
  overflow: auto;
  max-width: 100%;
  max-height: 100%;
}

.app-container {
  min-height: 100vh;
  max-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.main {
  flex-grow: 1;
  overflow-y: auto;
}

.main__wrapper {
  min-height: 1000vh;
}

.footer {
  box-shadow: 0px 1px 15px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navbar-item {
  font-size: 18px;
}
